body {
  height: 100vh;
  width: auto;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-image: url("https://source.unsplash.com/featured/?weather");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.city {
  font-size: 40px;
  font-weight: bold;
  margin-bottom: 10px;
}

.temp {
  font-size: 80px;
  font-weight: bold;
}

.description {
  font-size: 20px;
  font-weight: bold;
  margin-top: 10px;
}

.card {
  background: #9090908b;
  padding: 2em;
  border-radius: 20px;
  /*width: 100%;
  max-width: 420px;*/
  margin: 1em;
}

.search {
  display: flex;
  align-items: center;
  justify-content: center;
}

input.search-bar {
  border: none;
  padding: 0.8em;
  outline: none;
  border-radius: 20px;
}

button {
  margin: 0.5em;
  border-radius: 20px;
  border: none;
  width: 46px;
  width: 46px;
  cursor: pointer;
}

button:hover {
  background: #7c7c7c;
}

.flex {
  display: flex;
  align-items: center;
}

.description {
  text-transform: capitalize;
  margin-left: 10px;
}

.weather.loading {
  visibility: hidden;
  max-height: 20px;
  position: relative;
}

.weather.loading::after {
  visibility: visible;
  content: "Loading...";
  color: #222;
  position: absolute;
  top: 0;
}
